.tasks-container {

}

.tasks-container__wrapper {
  border-left: 1px solid @color-border;
  border-right: 1px solid @color-border;
}

.task__title-row {
  position: relative;
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(0deg, #e6eff3 0, #f4f8fa 100%);
  min-height: 54px;
  border-radius: 3px;
  padding-left: 20px;
  padding-right: 100px;
}

.task__title-row__wrapper:first-child {
  width: 50%;
  box-sizing: border-box;
  padding-top: 7px;
  position: relative;

  span {
    display: block;
    font-size: 13px;
  }

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #fff;
    position: absolute;
    top: 10%;
    right: 0;
  }
}

.task__title-row__wrapper:nth-child(2) {
  width: 50%;
  box-sizing: border-box;
  padding-top: 7px;
  padding-left: 21px;
  position: relative;

  span {
    display: block;
    font-size: 13px;
  }

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #fff;
    position: absolute;
    top: 10%;
    right: 0;
  }
}

.task__litera {
  color: @blue;
  display: inline-block;
  margin-left: 20px;
}

.task {
  position: relative;

  .form-project-tasks__item-respons-list {
    width: 224px;
  }

  .form-project-tasks__item-explanation-list {
    max-width: 945px;
    width: 100%;
  }
}

.task.form-project-tasks__item-inner--waiting-completion {
  .task__title-row {
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 16px;
      height: 16px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 20px;
      border-radius: 50%;
      background-color: #f6a522;
    }
  }

  .form-project-tasks__item-dscr-main {
    &::after {
      display: none;
    }
  }

  .form-project-tasks__status-switch {
    z-index: 10;
  }
}

.task.form-project-tasks__item-inner--failed {
  .task__title-row {
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 16px;
      height: 16px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 20px;
      border-radius: 50%;
      background-color: #c6353b;
    }
  }

  .form-project-tasks__item-dscr-main {
    &::after {
      display: none;
    }
  }

  .form-project-tasks__status-switch {
    z-index: 10;
  }
}

.task.form-project-tasks__item-inner--not-active {
  .task__title-row {
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 16px;
      height: 16px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 20px;
      border-radius: 50%;
      background-color: #dcdcdc;
    }
  }

  .form-project-tasks__item-dscr-main {
    &::after {
      display: none;
    }
  }

  .form-project-tasks__status-switch {
    z-index: 10;
  }
}
