.checkbox-reminder {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}
.checkbox-reminder + label {
  position: relative;
  padding: 0 0 0 60px;
  margin-left: 5px;
  cursor: pointer;
}
.checkbox-reminder + label::before {
  content: "";
  position: absolute;
  top: -4px;
  left: 10px;
  width: 36px;
  height: 22px;
  border-radius: 13px;
  background: #dce8ef;
  box-shadow: inset 0 2px 3px rgba(0, 0, 0, 0.2);
  transition: 0.2s;
}
.checkbox-reminder + label::after {
  content: "";
  position: absolute;
  top: -2px;
  left: 12px;
  width: 18px;
  height: 18px;
  border-radius: 10px;
  background: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  transition: 0.2s;
}
.checkbox-reminder:checked + label::before {
  background: #66a0e0;
}
.checkbox-reminder:checked + label::after {
  left: 26px;
}
.checkbox-reminder:focus + label::before {
  box-shadow: inset 0 2px 3px rgba(0, 0, 0, 0.2), 0 0 0 3px rgba(255, 255, 255, 0.5);
}
