  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 11px;
  position: relative;
}
.clients__table-item::after {
  content: "";
  display: block;
  height: 80%;
  width: 1px;
  background-color: #dce8ef;
  position: absolute;
  top: 10%;
  right: 0;
}
.clients__table-item:last-child::after {
  display: none;
}
.table-item--checkbox {
  max-width: 42px;
  padding-left: 10px;
}
.clients__new-client {
  display: none;
}
.edit-client {
  width: 548px;
  min-height: 404px;
  margin-left: -281px;
}
.edit-client__form {
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
}
.edit-client__form-name,
.edit-client__form-tel,
.edit-client__form-email {
  width: 100%;
  margin-bottom: 10px;
  padding-top: 8px;
  padding-bottom: 8px;
}
.edit-client__form-textarea {
  width: 100%;
  margin-bottom: 10px;
  min-height: 95px;
  padding-top: 8px;
  padding-bottom: 8px;
  font-family: "Helvetica", sans-serif;
  resize: none;
}
.edit-client__btn-submit {
  width: 100%;
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  margin-right: 20px;
  padding: 10px 14px;
  color: #ffffff;
  background-color: #66a0e0;
  cursor: pointer;
  transition: all ease 0.3s;
}
.edit-client__btn-submit:hover {
  opacity: 0.7;
}
.employees__wrapper {
  width: 100%;
  margin-bottom: 30px;
}
.employees__top {
  background-color: #fbfdfe;
  padding-top: 30px;
  padding-bottom: 30px;
}
.employees__wrapper-inner {
  padding-top: 30px;
}
.employees__title {
  margin-bottom: 15px;
}
.employees__switch {
  margin-bottom: 15px;
}
.employees__btn {
  font-size: 15px;
  line-height: 1;
  padding-bottom: 13px;
  border: none;
  color: #808e96;
  background-color: transparent;
  border-bottom: 2px solid transparent;
  transition: 0.3s;
  outline: transparent;
}
.employees__btn--active {
  border-bottom: 2px solid #66a0e0;
  color: #1d1d1b;
}
.employees__projects-btn-wrapper {
  border: 1px solid #dce8ef;
  border-radius: 3px;
  min-height: 82px;
  box-sizing: border-box;
  padding-left: 14px;
  padding-top: 26px;
  padding-right: 14px;
  padding-bottom: 16px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-content: space-between;
}
.employees__workspace-controls .controls__add-btn {
  width: 200px;
  text-align: center;
}
.employees__workspace-controls .controls__edit-txt-btn {
  width: 200px;
  text-align: center;
}
.employees__workspace-controls .close-btn {
  margin-left: auto;
}
.employees__table {
  border-bottom: 1px solid #dce8ef;
  margin-bottom: 20px;
}
.employees__table-title-row {
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(0deg, #e6eff3 0, #f4f8fa 100%);
  min-height: 54px;
  border-radius: 3px;
}
.employees__table-title-item {
  font-weight: 700;
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 17px;
  position: relative;
}
.employees__table-title-item::after {
  content: "";
  display: block;
  height: 80%;
  width: 1px;
  background-color: #fff;
  position: absolute;
  top: 10%;
  right: 0;
}
.employees__table-title-item:last-child::after {
  display: none;
}
.employees__table-row {
  display: flex;
  justify-content: flex-start;
  min-height: 43px;
  border-bottom: 1px solid #dce8ef;
  border-left: 1px solid #dce8ef;
  border-right: 1px solid #dce8ef;
}
.employees__table-row:last-child {
  border-bottom: 1px solid transparent;
}
.employees__table-item {
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 11px;
  position: relative;
}
.employees__table-item::after {
  content: "";
  display: block;
  height: 80%;
  width: 1px;
  background-color: #dce8ef;
  position: absolute;
  top: 10%;
  right: 0;
}
.employees__table-item:last-child::after {
  display: none;
}
.table-item--checkbox {
  max-width: 42px;
  padding-left: 10px;
}
.employees__table-row--waiting {
  color: #f6a522;
}
.employees__table-row--denial {
  color: #e30613;
}
.employees__wrapper-inner--access-denail .employees__table-title-row {
  border-radius: 3px 3px 0 0;
}
.employees__sub-title {
  font-weight: 300;
  font-size: 25px;
  margin-bottom: 30px;
}
.employees__reset-btn {
  background-color: #ffffff;
}
.employees__save-btn {
  background-color: #ffffff;
  margin-left: auto;
}
.employees__table-managerial-row {
  display: flex;
  background-color: #fbfdfe;
  min-height: 86px;
  border: 1px solid #dce8ef;
  margin-bottom: 10px;
}
.employees__table-managerial-row .employees__table-item span {
  display: block;
  margin-bottom: 15px;
  padding-top: 7px;
}
.employees__table-managerial-row .employees__table-item:last-child {
  padding-top: 54px;
}
.employess__messengers-label {
  font-size: 13px;
  display: inline-block;
  vertical-align: middle;
}
.employess__messengers-checkbox {
  display: inline-block;
  vertical-align: middle;
}
.employees__table-item--sel-mess {
  padding-left: 9px;
  padding-right: 9px;
}
.employees__table-item--sel-mess .cd-dropdown {
  margin-bottom: 10px;
}
.employees__table-item--sel-mess .cd-dropdown span {
  padding-top: 5px;
}
.employees__table-managerial-rows-wrapper {
  border: 1px solid #dce8ef;
}
.employees__table-managerial-rows-wrapper .employees__table-row {
  border-left: none;
  border-right: none;
}
.employees--faded {
  display: none;
}
