.project-pages {
  margin-bottom: 30px;
}

.project-pages__wrapper-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.project-pages__wrapper-middle {
  margin-top: -50px;
}

.project-pages__btn {
  font-size: 15px;
  line-height: 1;
  padding-bottom: 13px;
  border: none;
  color: #808e96;
  background-color: transparent;
  border-bottom: 2px solid transparent;
  transition: all ease .3s;
  outline: transparent;

  &:hover {
    opacity: 0.7;
  }
}

.project-pages__btn--active {
  border-bottom: 2px solid @blue;
  color: @color-text;
}

.project-pages__edit-wrapper {
  max-width: 425px;
  width: 100%;
  display: flex;
  margin-left: auto;

  .project-pages__edit-btn--cancellation {
    display: none;
  }

  .project-pages__edit-btn--save {
    display: none;
  }
}

.project-pages__edit-btn {
  transition: all ease .3s;
  max-width: 200px;
  width: 100%;

  &:hover {
    opacity: 0.7;
  }
}

.project-pages__edit-btn--edit {
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  margin-right: 20px;
  color: #66a0e0;
  padding: 10px 14px;
  background-color: transparent;
  display: block;
  margin-left: auto;
}

.project-pages__edit-btn--cancellation {
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  margin-right: 15px;
  color: #66a0e0;
  padding: 10px 14px;
  background-color: transparent;
}

.project-pages__edit-btn--save {
  position: relative;
  color: #ffffff;
  background-color: #66a0e0;
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  padding: 10px 14px;
}

.project-pages__edit--active {
  .project-pages__edit-btn--edit {
    display: none;
  }

  .project-pages__edit-btn--cancellation {
    display: block;
  }

  .project-pages__edit-btn--save {
    display: block;
  }
}

.project-pages--comments-files {
  padding-left: 20px;
  padding-top: 15px;
  padding-right: 20px;
  padding-bottom: 45px;
}

.project-pages__switch--comments-files {
  .project-pages__btn--active {
    border: none;
  }
}
