.form-project-finance__wrapper-inner {
  border-top: 1px solid @color-border;
  padding-top: 30px;
}

.form-project-finance__date-wrapper {
  max-width: 382px;
  display: flex;
  justify-content: space-between;
}

.form-project-finance__wrapper-inner-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 32px;

  .cd-dropdown {
    width: 100%;
    max-width: 218px;
  }
}

.form-project-finance__exp-xls {
  margin-right: 0;
}

.form-project-finance__wrapper-inner-middle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;
}

.form-project-finance__btn {
  width: 100%;
  max-width: 148px;
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  margin-right: 20px;
  color: #66a0e0;
  padding: 10px 14px;
  background-color: transparent;
}

.form-project-finance__btn--coming {
  color: #40bf70;
  border-color: #40bf70;
}

.form-project-finance__btn--consumption {
  color: #e30613;
  border-color: #e30613;
}

.form-project-finance__btn--calculate {
  color: #dce8ef;
  border-color: #dce8ef;
}

.form-project-finance__info {
  padding-left: 20px;
  padding-right: 20px;
  border: 1px solid  @color-border;
  background-color: @color-background;
  min-height: 40px;
  border-radius: 3px;
  width: 100%;
  max-width: 562px;
  display: flex;
  justify-content: space-between;
  padding-top: 9px;
}

.form-project-finance__info-wrapper {
  display: inline-block;
}

.form-project-finance__info-sum--minus {
  &::before {
    content: "-";
  }
}

.form-project-finance__info-sum--plus {
  &::before {
    content: "+";
  }
}

.form-project-finance__info-sum--green {
  color: #40bf70;
}

.form-project-finance__info-sum--red {
  color: #e30613;
}

.form-project-finance__info-sum--rub {
  &::after {
    content: "\20BD"
  }
}

.form-project-finance__info-sum--usa {
  &::after {
    content: "\0024"
  }
}

.form-project-finance__info-sum--eur {
  &::after {
    content: "\20AC"
  }
}

.form-project-finance__info-btn-close {
  height: 14px;
  width: 14px;
  display: inline-block;
  background-image: url(../img/icons/btn-close-icon.png);
  background-position: 50%;
  background-size: cover;
  background-repeat: no-repeat;
  border: none;
  background-color: transparent;
  margin-top: 3px;
}

.form-project-finance__btn-close {
  background-color: transparent;
}

.form-project-finance__table-title-row {
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(0deg,#e6eff3 0,#f4f8fa 100%);
  min-height: 54px;
  border-radius: 3px;
}

.form-project-finance__table-title-item {
  font-weight: 700;
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 17px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #fff;
    position: absolute;
    top: 10%;
    right: 0;
  }
}

.form-project-finance__table-row {
  display: flex;
  justify-content: flex-start;
  min-height: 43px;
  border-bottom: 1px solid @color-border;

  &:last-child {
    border-bottom: 1px solid transparent;
  }
}

.form-project-finance__table-item {
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 11px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: @color-border;
    position: absolute;
    top: 10%;
    right: 0;
  }
}

.table-item--checkbox {
  max-width: 42px;
  padding-left: 10px;
}

.table-item--date {

}

.table-item--sum {

}

.table-item--comment {
  flex-grow: 1;
}

.table-item--name {
  flex-grow: 1;

  &::after {
    display: none;
  }
}
