.breadcrumbs {
  margin-bottom: 20px;
}

.breadcrumbs__list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.breadcrumbs__list-item {
  position: relative;

  &::after {
    content: "/";
    color: @color-text;
    margin: 0 10px;
  }

  &:last-child {
    &::after {
      content: "";
    }
  }
}

.breadcrums__list-link {
  color: @blue;
  text-decoration: underline;
}
