.employees__wrapper {
  width: 100%;
  margin-bottom: 30px;
}

.employees__top {
  background-color: @color-background;
  padding-top: 30px;
  padding-bottom: 30px;
}

.employees__wrapper-inner {
  padding-top: 30px;
}

.employees__title {
  margin-bottom: 15px;
}

.employees__switch {
  margin-bottom: 15px;
}

.employees__btn {
  font-size: 15px;
  line-height: 1;
  padding-bottom: 13px;
  border: none;
  color: #808e96;
  background-color: transparent;
  border-bottom: 2px solid transparent;
  transition: 0.3s;
  outline: transparent;
}

.employees__btn--active {
  border-bottom: 2px solid #66a0e0;
  color: #1d1d1b;
}

.employees__projects-btn-wrapper {
  border: 1px solid @color-border;
  border-radius: 3px;
  min-height: 82px;
  box-sizing: border-box;
  padding-left: 14px;
  padding-top: 26px;
  padding-right: 14px;
  padding-bottom: 16px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-content: space-between;
}

.employees__workspace-controls {
  .controls__add-btn {
    width: 200px;
    text-align: center;
  }

  .controls__edit-txt-btn {
    width: 200px;
    text-align: center;
  }

  .close-btn {
    margin-left: auto;
  }
}

.employees__table {
  border-bottom: 1px solid @color-border;
  margin-bottom: 20px;
}

.employees__table-title-row {
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(0deg,#e6eff3 0,#f4f8fa 100%);
  min-height: 54px;
  border-radius: 3px;
}

.employees__table-title-item {
  font-weight: 700;
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 17px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #fff;
    position: absolute;
    top: 10%;
    right: 0;
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}

.employees__table-row {
  display: flex;
  justify-content: flex-start;
  min-height: 43px;
  border-bottom: 1px solid @color-border;
  border-left: 1px solid @color-border;
  border-right: 1px solid @color-border;

  &:last-child {
    border-bottom: 1px solid transparent;
  }
}

.employees__table-item {
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 11px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: @color-border;
    position: absolute;
    top: 10%;
    right: 0;
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}

.table-item--checkbox {
  max-width: 42px;
  padding-left: 10px;
}

.employees__table-row--waiting {
  color: #f6a522;
}

.employees__table-row--denial {
  color: #e30613;
}

// настройки доступа


.employees__wrapper-inner--access-denail {
  .employees__table-title-row {
    border-radius: 3px 3px 0 0;
  }
}

.employees__sub-title {
  font-weight: 300;
  font-size: 25px;
  margin-bottom: 30px;
}

.employees__reset-btn {
  background-color: #ffffff;
}

.employees__save-btn {
  background-color: #ffffff;
  margin-left: auto;
}

.employees__table-managerial-row {
  display: flex;
  background-color: @color-background;
  min-height: 86px;
  border: 1px solid @color-border;
  margin-bottom: 10px;

  .employees__table-item span {
    display: block;
    margin-bottom: 15px;
    padding-top: 7px;
  }

  .employees__table-item:last-child {
    padding-top: 54px;
  }
}

.employess__messengers-label {
  font-size: 13px;
  display: inline-block;
  vertical-align: middle;
}

.employess__messengers-checkbox {
  display: inline-block;
  vertical-align: middle;
}

.employees__table-item--sel-mess {
  padding-left: 9px;
  padding-right: 9px;

  .cd-dropdown {
    margin-bottom: 10px;

    span {
      padding-top: 5px;
    }
  }
}

.employees__table-managerial-rows-wrapper {
  border: 1px solid @color-border;

  .employees__table-row {
    border-left: none;
    border-right: none;
  }
}

.employees--faded {
  display: none;
}
