.form-project-file-comment {
  position: relative;
}

.form-project-file-comment__fieldset {
  border: none;

  legend {

  }
}

.form-project-file-comment__info-area {
  width: 100%;
  min-height: 182px;
  border: 2px dashed transparent;
  position: relative;
  background-color: #f8fafc;
}

.form-project-file-comment__input-file {
  cursor: pointer;
  position: absolute;
  height: 100%;
  width: 100%;
  font-size: 0;
  opacity: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.hover {
  border: 2px dashed @blue;
  background-color: rgba(220, 232, 239, 0.6);
  opacity: 1;
  position: relative;
  z-index: 1;

  &::before {
    content: "Поместите файл для отправки";
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgba(220, 232, 239, 0.9);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    color: @blue;
    font-size: 25px;
    line-height: 1;
    background-image: url(../img/icons/file-drop-icon.png);
    background-position: 37% 53%;
    background-size: 21px 27px;
    background-repeat: no-repeat;
    padding-top: 12%;
    padding-left: 40%;
    box-sizing: border-box;
  }
}

.dropped {

}

.form-project-file-comment__list {
  padding-left: 20px;
  padding-top: 20px;
  position: relative;
}

.form-project-file-comment__list-item {
  max-width: 440px;
  width: 100%;
  background-color: #ffffff;
  position: relative;
  padding: 10px 15px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  border-radius:  3px 3px 3px 0;

  &::after {
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    border: 5px solid transparent;
    border-right: 5px solid #ffffff;
    border-bottom: 5px solid #ffffff;
  }
}

.form-project-file-comment__list-name,
.form-project-file-comment__list-comment,
.form-project-file-comment__list-file {
  display: block;
  margin-bottom: 7px;
}

.form-project-file-comment__list-date-time {
  margin-left: auto;
  color: @blue;
}

.form-project-file-comment__list-file span {
  position: relative;
  padding-left: 22px;

  &::after {
    content: "";
    display: block;
    width: 19px;
    height: 21px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../img/icons/file-icon.png);
    background-position: 50%;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.form-project-file-comment__submit-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form-project-file-comment__input-text {
  width: 100%;
  padding-left: 47px;
  padding-top: 13px;
  padding-bottom: 12px;
  background-image: url(../img/icons/file-input-icon.png);
  background-position: 14px  12px;
  background-size: 19px 18px;
  background-repeat: no-repeat;

  &::placeholder {
    color: #bbbbbb;
  }
}

.form-project-file-comment__submit-btn {
  width: 132px;
  height: 42px;
  position: relative;
  color: #fff;
  background-color: #66a0e0;
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  padding: 10px 14px;
}
