.form-project-tasks__wrapper-inner {
  border-top: 1px solid @color-border;
  padding-top: 20px;
}

.form-project-tasks__navigation {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
}

.form-project-tasks__navigation-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  max-width: 585px;
  width: 100%;

  .form-project-data__fieldset-wrapper {
    max-width: 455px;
    margin-right: 20px;

    .cd-dropdown {
      max-width: 100%;
    }
  }

  .contols__arrow--close {
    margin-right: 0;
  }
}

.form-project-tasks__navigation-wrapper:first-child {
  align-items: center;
}

.form-project-tasks__task-item {
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.form-project-tasks__title-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-image: linear-gradient(0deg, rgb(230, 239, 243) 0%, rgb(244, 248, 250) 100%);
  min-height: 54px;
  border-radius: 3px;
  padding-left: 20px;
  padding-right: 20px;
}

.form-project-tasks__title-group {
  font-size: 13px;
  display: block;
}

.form-project-tasks__title-name {
  font-size: 15px;
  font-weight: bold;
}

.form-project-tasks__title-wrapper:nth-child(2) {
  margin-left: auto;
}

.form-project-tasks__title-button {
  font-weight: bold;
  color: @blue;
  background-color: transparent;
  border: none;
}

.form-project-tasks__title-dropdown-btn {
  margin-left: 20px;
  position: relative;
  width: 21px;
  height: 21px;
  background-color: transparent;
  border: none;

  &::after {
    content: "";
    position: absolute;
    left: 5px;
    top: 8px;
    width: 12px;
    height: 7px;
    background-image: url(../img/icons/select-arrow-icon.png);
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
  }
}

.form-project-tasks__title-dropdown-btn--arrow-up {
  transform: rotate(180deg);
}

.form-project-tasks__status-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 54px;
  padding-left: 20px;
  padding-right: 20px;
}

.form-project-tasks__status-name {
  font-weight: bold;

  &::after {
    content: " / ";
  }
}

.form-project-tasks__status-count {
  color: @blue;
}

.form-project-tasks__new-task-add-btn {
  border-radius: 50%;
  border: 1px solid @blue;
  width: 30px;
  height: 30px;
  position: relative;
  margin-right: 10px;
  background-color: transparent;

  &::after {
    content: "";
    position: absolute;
    left: 7px;
    top: 7px;
    width: 14px;
    height: 14px;
    background-image: url(../img/icons/blue-plus-icon.png);
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
  }
}

.form-project-tasks__item-dscr-main {
  padding-top: 17px;
  padding-left: 20px;
  padding-right: 51px;
  padding-bottom: 15px;
  min-height: 74px;
  position: relative;
  border-bottom: 1px solid @color-border;
  background-color: #fbfdfe;
}

.form-project-tasks__item-dscr-txt {
  font-weight: bold;
}

.form-project-tasks__item-inner {
  margin-bottom: 20px;
  border-top: 1px solid @color-border;
  border-bottom: 1px solid @color-border;
}

.form-project-tasks__item-inner--failed {
  .form-project-tasks__item-dscr-main {
    background-color: #fcf3f3;

    &::after {
      content: "";
      display: block;
      width: 16px;
      height: 16px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 20px;
      border-radius: 50%;
      background-color: #c6353b;
    }
  }

  .form-project-tasks__item-respons-list {
    background-color: #fcf3f3;
  }

  .form-project-tasks__item-respons-item:last-child {
    .form-project-tasks__item-respons-upper-text {
      position: relative;

      &::after {
        content: "";
        display: block;
        width: 16px;
        height: 16px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 20px;
        border-radius: 50%;
        background-color: #c6353b;
      }
    }
  }
}

.form-project-tasks__item-inner--waiting-completion {
  .form-project-tasks__item-dscr-main {
    background-color: #fffff2 ;

    &::after {
      content: "";
      display: block;
      width: 16px;
      height: 16px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 20px;
      border-radius: 50%;
      background-color: #f6a522;
    }
  }

  .form-project-tasks__item-respons-list {
    background-color: #fffff2;
  }

  .form-project-tasks__item-respons-item:last-child {
    .form-project-tasks__item-respons-upper-text {
      position: relative;

      &::after {
        content: "";
        display: block;
        width: 16px;
        height: 16px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 20px;
        border-radius: 50%;
        background-color: #f6a522;
      }
    }
  }
}

.form-project-tasks__item-info {
  display: flex;

}

.form-project-tasks__item-respons-list {
  width: 224px;
  padding-left: 5px;
  padding-right: 5px;
  border-right: 1px solid @color-border;
  background-color: #fbfdfe;
}

.form-project-tasks__item-respons-item {
  min-height: 62px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 11px;
  padding-bottom: 12px;
  border-bottom: 1px solid @color-border;

  &:last-child {
    border-bottom: 0;
  }
}

.form-project-tasks__item-respons-low-text {
  font-size: 13px;
}

.form-project-tasks__item-explanation-list {
  flex-grow: 1;
}

.form-project-tasks__item-explanation-title-row {
  min-height: 32px;
  padding-left: 20px;
  padding-top: 5px;
  font-weight: bold;
  background-image: linear-gradient(0deg, rgb(230, 239, 243) 0%, rgb(244, 248, 250) 100%);
}

.form-project-tasks__item-explanation-dscr {
  font-family: "Helvetica", sans-serif;
  font-size: 15px;
  line-height: 23px;
  font-weight: 400;
  color: @color-text;
  background-color: #ffffff;
  height: 40px;
  width: 100%;
  box-sizing: border-box;
  padding-left: 20px;
  padding-top: 9px;
  resize: none;
  overflow-y: hidden;
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  vertical-align: bottom;
}

.form-project-tasks__item-explanation-dscr.form-project-data--active-element {
  border-color: @blue;
}

.form-project-tasks__edit-title-row {
  min-height: 32px;
  padding-left: 20px;
  padding-top: 5px;
  font-weight: bold;
  background-image: linear-gradient(0deg, rgb(230, 239, 243) 0%, rgb(244, 248, 250) 100%);

  span {
    font-weight: bold;
  }
}

.form-project-tasks__edit-status {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  border-radius: 50%;
}

.form-project-tasks__edit-status--failed {
  background-color: #c6353b;
}

.form-project-tasks__edit-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 78px;
  padding-left: 20px;
  padding-right: 20px;
}

.form-project-tasks__edit-wrapper-inner {
  display: flex;
  align-items: center;
  max-width: 550px;
  width: 100%;

  .controls__edit-btn {
    margin-right: 10px;
  }

  .controls__close-btn {
    margin-right: 10px;
  }
}

.form-project-tasks__edit-label {
  margin-right: 35px;
}

.form-project-tasks__edit-btn {
  max-width: 268px;
  width: 100%;
  font-weight: bold;
  border: 1px solid @color-border;
  border-radius: 3px;
  background-color: transparent;
}

.form-project-tasks__edit-btn--completed {
  color: #40bf70;
  min-height: 40px;
  border-color: #40bf70;
  margin-right: auto;
}

.form-project-tasks__edit-btn--failed {
  color: #e30613;
  min-height: 40px;
  border-color: #e30613;
}

.form-project-tasks__status-switch {
  display: none;
  width: 65px;
  right: 20px;
  justify-content: space-between;
  align-items: center;
  top: 50%;
  margin-top: -8px;
  margin-left: auto;
  position: absolute;
}

.form-project-tasks__status-btn {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: none;
  position: relative;
}

.form-project-tasks__status-btn--failed {
  background-color: #c6353b;
}

.form-project-tasks__status-btn--waiting-completion {
  background-color: #fff702;
}

.form-project-tasks__status-btn--not-active {
  background-color: #dcdcdc;
}

.form-project-tasks__edit-wrapper {
  display: none;
}

.form-project-tasks__close-item-btn {
  display: none;
  z-index: 2001;
  height: 32px;
  width: 32px;
  border-radius: 50%;
  border: 1px solid @color-border;
  background-color: #ffffff;
  position: absolute;
  top: -9px;
  right: -5px;

  &::after {
    content: "";
    display: block;
    height: 18px;
    width: 2px;
    background-color: @blue;
    transform: rotate(-45deg);
    position: absolute;
    top: 6px;
    left: 14px;
  }

  &::before {
    content: "";
    display: block;
    height: 18px;
    width: 2px;
    background-color: @blue;
    transform: rotate(45deg);
    position: absolute;
    top: 6px;
    left: 14px;
  }
}

.form-project-tasks__item-inner--active {
  position: relative;
  z-index: 2000;
  box-shadow: 0 20px 30px 0 rgba(18, 59, 59, 0.09);

  .form-project-tasks__close-item-btn {
    display: block;
    z-index: 2001;
  }

  .form-project-tasks__status-switch {
    display: flex;
  }

  .form-project-tasks__item-dscr-main {
    padding-right: 85px;

    &::after {
      display: none;
    }
  }

  .form-project-tasks__edit-wrapper {
    display: block;
  }
}

template {
  display: none;
}
