.form-project-notes__wrapper-inner {
  border-top: 1px solid #dce8ef;
  padding-top: 20px;
}

.form-project-notes__search-fieldset {
  border: none;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.notes__add-btn {
  position: relative;
  color: #ffffff;
  background-color: #66a0e0;
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  padding: 10px 14px;
  margin-right: 20px;
}

.form-project-notes__search-input {
  flex-grow: 1;
}

.form-project-notes__note {
  border-radius: 0 0 3px 3px;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;

    .form-project-notes__note-wrapper {
      border-bottom: none;
      padding-bottom: 40px;
    }
  }
}

.form-project-notes__note-title-row {
  min-height: 32px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 5px;
  font-weight: 700;
  background-image: linear-gradient(0deg, #e6eff3 0, #f4f8fa 100%);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-project-notes__title-dropdown-btn {
  margin-left: 20px;
  position: relative;
  width: 21px;
  height: 21px;
  background-color: transparent;
  border: none;

  &::after {
    content: "";
    position: absolute;
    left: 5px;
    top: 8px;
    width: 12px;
    height: 7px;
    background-image: url(../img/icons/select-arrow-icon.png);
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
  }
}

.form-project-notes__note-info {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .edit-btn,
  .close-btn {
    background-color: transparent;
    border-color: transparent;
    padding: 0;
    width: 21px;
    margin-left: 20px;

    &::after {
      left: auto;
      right: 0;
    }
  }
}

.form-project-notes__note-author {
  margin-right: 20px;
}

.form-project-notes__note-date {
  margin-right: auto;
  color: @blue;
}

.form-project-notes__note-wrapper {
  padding: 0 20px;
  padding-bottom: 15px;
  background-color: @color-background;
  border-bottom: 1px solid @color-border;
  border-radius: 0 0 3px 3px;
}

.form-project-notes__note-dscr {
  font-family: "Helvetica", sans-serif;
  font-size: 15px;
  line-height: 23px;
  font-weight: 400;
  color: #1d1d1b;
  height: 40px;
  width: 100%;
  box-sizing: border-box;
  padding-left: 0;
  padding-top: 9px;
  resize: none;
  overflow-y: hidden;
  vertical-align: bottom;
  border: 1px solid transparent;
  background-color: @color-background;
}

.form-project-notes__note-dscr.form-project-data--active-element {
  border: 1px solid @blue;
}

.form-project-notes__note-file {
  position: relative;
  padding-left: 45px;
  padding-top: 14px;
  padding-bottom: 15px;
  max-width: 900px;
  width: 100%;
  background-color: #ffffff;
  border-radius: 3px;

  &::after {
    content: "";
    display: block;
    width: 19px;
    height: 21px;
    position: absolute;
    top: 14px;
    left: 20px;
    background-image: url(../img/icons/file-icon.png);
    background-position: 50%;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

