.edit-task-popup {
  display: none;
  width: 100%;
  min-height: 500px;
  max-width: 1170px;
  left: 50%;
  right: auto;
  transform: translateX(-50%);
  padding-bottom: 40px;
}

.edit-task-popup__form-wrapper {
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;
}

.edit-task-popup__wrapper--group {
  display: flex;
  align-items: flex-end;

  .edit-task-popup__form-wrapper-inner {
    flex-grow: 1;

    span {
      display: block;
      margin-bottom: 5px;
    }
  }

  .cd-dropdown {
    max-width: 100%;
    width: 100%;
  }
}

.edit-task-popup__btn-add-group {
  width: 200px;
  margin-right: 0;
  margin-left: 20px;
  height: 40px;
  background-color: transparent;
}

.edit-task-popup__wrapper-data--checkboxes {
  display: flex;
  align-items: flex-end;

  .edit-task-popup__form-wrapper-inner {
    margin-right: auto;

    &:last-child {
      margin-right: 0;
    }
  }

  span {
    display: block;
  }

  .edit-task-popup__reminder {
    display: inline-block;
  }

  .yellow-chekbox:checked {
  }
}

.edit-task-popup__form-wrapper-inner--responsible {
  max-width: 246px;
  width: 100%;
}

.edit-task-popup__label {
  margin-right: 5px;
}

.edit-task-popup__wrapper--task {
  position: relative;

  span {
    display: block;
  }
}

.edit-task-popup__wrapper--comment {
  position: relative;

  span {
    display: block;
  }
}

.edit-task-popup__textarea {
  width: 100%;
  min-height: 98px;
  border-radius: 10px;
  border: 1px solid @color-border;
  padding: 10px;
  resize: none;
  font-family: "Helvetica", sans-serif;
  font-size: 15px;
  line-height: 21px;

  &::placeholder {
    font-family: "Helvetica", sans-serif;
    font-size: 15px;
    line-height: 21px;
  }
}

.edit-task-popup__micro-btn {
  position: absolute;
  top: 25px;
  right: 25px;
  border: none;
  background-color: transparent;

  &::after {
    content: "";
    display: block;
    width: 17px;
    height: 27px;
    background-image: url(../img/icons/micro-icon.png);
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
  }
}

.edit-task-popup__wrapper--files {
  h3 {
    font-weight: 300;
    font-size: 21px;
    line-height: 2;
    display: inline-block;
    margin-right: 20px;
    vertical-align: bottom;
  }
}

.edit-task-popup__input-file-wrapper {
  display: inline-block;
  min-height: 40px;
  max-width: 500px;
  width: 100%;
  vertical-align: bottom;
  position: relative;

  &::before {
    content: "Выберите файл";
    display: inline-block;
    vertical-align: bottom;
    border: 1px solid @blue;
    text-align: center;
    max-width: 200px;
    width: 100%;
    height: 40px;
    padding-top: 9px;
    border-radius: 3px;
    color: @blue;
    box-sizing: border-box;
    margin-right: 20px;
  }

  &::after {
    content: "или перетащите в эту область";
    display: inline-block;
    vertical-align: bottom;
    border: 1px dashed @blue;
    text-align: center;
    max-width: 252px;
    width: 100%;
    height: 40px;
    padding-top: 9px;
    border-radius: 3px;
    color: @blue;
    box-sizing: border-box;
  }
}

.edit-task-popup__input-file {
  color: @blue;
  position: absolute;
  height: 100%;
  width: 100%;
  font-size: 0;
  opacity: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.edit-task-popup__files-list {
  margin-bottom: 20px;
}

.edit-task-popup__files-list-item {
  padding-top: 12px;
  padding-bottom: 10px;
  border-bottom: 1px solid @color-border;
  display: flex;
  align-items: center;

  &:last-child {
    border-bottom: none;
  }
}

.edit-task-popup__file-img {
  margin-right: 20px;
}

.edit-task-popup__file-close {
  margin-left: auto;
  background-color: transparent;
}
