.form-project-data_new-client-btn {
  position: relative;
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  color: #66a0e0;
  padding: 10px 14px;
  padding-left: 30px;
  background-color: transparent;
  max-width: 200px;
  width: 100%;
  margin-top: 30px;

  &::before {
    content: "+";
    position: absolute;
    left: 35px;
    top: 7px;
    font-size: 21px;
  }
}

.form-project-data__label {
  font-weight: bold;
  display: block;
  margin-bottom: 10px;
}

.form-project-data__fieldset {
  padding-left: 20px;
  padding-right: 20px;
  border: none;
}

.form-project-data__fieldset-wrapper {
  max-width: 550px;
  width: 100%;
}

.form-project-data__textarea {
  width: 100%;
  resize: vertical;
  background-color: #ffffff;

  &::placeholder {
    font-family: "Helvetica", sans-serif;
    font-size: 15px;
    line-height: 21px;
  }
}

.form-project-data__fieldset-wrapper-essense {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  align-content: space-between;
  margin-bottom: 30px;

  .form-project-data__fieldset-wrapper:nth-child(2) {
    max-width: 342px;
  }

  .cd-dropdown {
    max-width: 342px;
    display: inline-block;

    ul {
      position: absolute;
    }
  }
}

.form-project-data__wrapper-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid @color-border;
  padding-top: 20px;

  .form-project-data__fieldset {
    max-width: 50%;
    width: 100%;

    legend {
      font-size: 25px;
      line-height: 1;
      font-weight: 300;
      margin-bottom: 15px;
    }

    .form-project-data__input-text {
      width: 100%;
      margin-bottom: 15px;
      background-color: #ffffff;
    }
  }
}

.form-project-data--active-element {
  border-color: @blue;
}
